import React, { useEffect, useState, useContext, Component } from 'react'
import { Link } from 'gatsby'
import styled from '@emotion/styled'
import { motion, useViewportScroll  } from 'framer-motion'
import { useSiteMetadata } from '../hooks/use-site-metadata'
import ColorToggle from './ColorToggle'
import OptionsContext from '../components/OptionsContext'

//import { render } from "react-dom"
//import $ from "jquery"

const Header = styled.header`
  font-family: ${props => props.theme.fonts.body};
  transition: max-height 0.5s cubic-bezier(0.52, 0.16, 0.24, 1), border 0.3s;
//background: ${props => props.theme.colors.background};
  background: ${props => props.theme.colors.navbar};  
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-height: ${props => (props.open ? '100%' : '60px')};
  width: 100%;
  z-index: 99;
  display: flex;
//border-bottom: 1px solid ${props => props.theme.colors.border};
  //border-bottom: 1px solid ${props => 'red'};
  border-bottom: 1px solid ${props => 'red'};
  border-top: 1px solid ${props => 'red'};
  border-style: none none double none;
  border-width: ${props => (props.open ? '0' : '1px')};
  
  // this alters the border upon resize. I dont want the border to go away
  @media screen and (min-width: ${props => props.theme.responsive.medium}) {
    max-height: 60px;
    //border-width: 10px;
  }
  border-width: 10px;

  transition: opacity 0.5s;
  opacity: ${props => (props.show ? '1' : '0')};
`
const Nav = styled.nav`
  max-width: ${props => props.theme.sizes.maxWidth};
  width: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;
  bottom: 20px
  position: relative;
`

const List = styled.ul`
  position: relative;
  padding: 4rem 0 0 0;
  pointer-events: ${props => (props.open ? 'auto' : 'none')};
  @media screen and (min-width: ${props => props.theme.responsive.medium}) {
    pointer-events: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    padding: 0;
  }
`

const Item = styled(motion.li)`
  display: block;
  padding: 0.5rem 0;
  @media screen and (min-width: ${props => props.theme.responsive.medium}) {
    padding: 0;
    //line-height: 60px;
    line-height: 60px;
    display: inline-block;
    margin: 0 0 0 1.5rem;
    opacity: 1 !important;
    visibility: visible !important;
  }
  &:first-of-type {
    padding: 0;
    pointer-events: auto;
    //line-height: 60px;
    line-height: 60px;
    opacity: 1 !important;
    visibility: visible !important;
    font-weight: ${props => props.theme.fonts.boldWeight};
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    @media screen and (min-width: ${props => props.theme.responsive.medium}) {
      position: relative;
      flex: 1;
      margin: 0;
    }
  }
  a {
    position: relative;
    text-decoration: none;
    //color: ${props => props.theme.colors.text};
    color: ${props => props.theme.colors.navbarText};
    transition: all 0.3s;
    &:hover {
      color: ${props => props.theme.colors.highlight};
    }
    @media (hover: none) {
      //color: ${props => props.theme.colors.text} !important;
      color: ${props => props.theme.colors.navbarText} !important;
    }
  }
`

const Toggle = styled.button`
  margin: 0;
  padding: 0;
  z-index: 999;
  transition: transform 0.3s;
  cursor: pointer;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 1.5rem;
  width: 1.5rem;
  height: 60px;
  @media screen and (min-width: ${props => props.theme.responsive.medium}) {
    display: none;
  }
  span {
    transition: all 0.3s;
    display: block;
    //background: ${props => props.theme.colors.text};
    background: ${props => props.theme.colors.navbarText};
    width: 100%;
    height: 2px;
  }
  span:first-of-type {
    transform: rotate(${props => (props.open ? '45deg' : '0')})
      translateY(${props => (props.open ? '0' : '.35rem')});
  }
  span:nth-of-type(2n) {
    transform: rotate(${props => (props.open ? '-45deg' : '0')})
      translateY(${props => (props.open ? '0' : '-.35rem')});
    position: relative;
    bottom: ${props => (props.open ? '2px' : '0')};
  }


`
